:root {
  --size-footer-content: 80px;
  --copyright-color: #bab8b8;
  --base-min-width: 1400px;
  --font-family-header-pages: playfair display;
  --font-size-header-pages: 46px; }

* {
  box-sizing: border-box; }

html {
  color: #111111;
  font-size: 10px;
  font-family: roboto, NavMenuFonts, playfair display;
  font-family: "Playfair Display", serif;
  font-family: "Roboto", sans-serif; }

body,
ul,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure {
  margin: 0;
  padding: 0; }

ul,
ol {
  list-style: none; }

a {
  text-decoration: none; }

input,
button {
  font-size: 1rem; }

.text-bold {
  font-weight: 800; }

.text-regular {
  font-weight: 500; }

.page-main {
  display: flex;
  justify-content: center; }

.no-padding {
  padding: 0 !important; }

.body-page {
  min-width: 100vw;
  min-height: calc(100vh - 120px);
  margin: 0 auto; }

.content-wrapper {
  overflow: hidden;
  width: 100%;
  padding: 0 1rem;
  max-width: calc(980px + 2rem);
  margin: 0 auto; }

.page-content {
  min-width: 100vw;
  margin: 0 auto;
  min-height: calc(100vh - calc(var(--size-footer-content) + 80px)); }

@font-face {
  font-family: "roboto";
  src: url(fonts/KFOlCnqEu92Fr1MmEU9fBBc4AMP6lQ.woff2) format("woff2");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: "NavMenuFonts";
  src: url(fonts/0078f486-8e52-42c0-ad81-3c8d3d43f48e.woff2) format("woff2");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: "playfair display";
  src: url(fonts/nuFiD-vYSZviVYUb_rj3ij__anPXDTzYgEM86xQ.woff2) format("woff2");
  font-weight: 300;
  font-style: normal; }

.grid-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

