.page-space {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex-wrap: nowrap;
  flex-grow: 0;
  flex-shrink: 1;
  min-width: var(--base-min-width);
  height: 100%; }
  .page-space__description {
    background: #3eccb5;
    min-height: 242px;
    height: 50%; }
  .page-space__images {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 100%; }

.space-description {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center; }

.space-header {
  font-family: var(--font-family-header-pages), serif;
  font-size: var(--font-size-header-pages);
  margin: 65px 0 20px 0; }
  .space-header__header {
    font-weight: 600;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: #fff; }

.space-text {
  text-align: center;
  padding-bottom: 10px; }
  .space-text__descr {
    font-family: NavMenuFonts;
    font-size: 15px;
    font-weight: 100;
    line-height: 1.7em;
    color: #fff; }

.space-images {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: nowrap; }
  .space-images__content {
    width: 50%; }

.images-content {
  position: relative;
  width: 100%;
  height: 395px; }
  .images-content__back {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    font-size: 48px;
    height: 395px;
    width: 100%;
    visibility: hidden; }

.content-back__text {
  transform: translate(0%, -50%);
  margin-bottom: 80px; }

.content-back__bottom {
  display: flex;
  justify-content: space-between;
  width: 100%; }
  .content-back__bottom_like {
    padding-left: 50px;
    padding-bottom: 25px; }
  .content-back__bottom_link {
    padding-right: 50px;
    padding-bottom: 25px; }

.back-text {
  color: #fff; }
  .back-text__header {
    font-family: var(--font-family-header-pages);
    font-size: 38px;
    font-weight: 900;
    text-transform: uppercase; }

.bgimg__img1 {
  background-image: url(images/d1c5cd_0.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden; }

.bgimg__img2 {
  background-image: url(images/d1c5cd_1.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden; }

.bgimg__img3 {
  background-image: url(images/d1c5cd_2.jpg);
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
  overflow: hidden; }

.bgimg__img4 {
  background-image: url(images/d1c5cd_3.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden; }

.img-jpg {
  position: relative; }
  .img-jpg:hover {
    opacity: 1; }
  .img-jpg:hover .images-content__back {
    visibility: visible;
    background: rgba(33, 33, 33, 0.8);
    color: #fff; }
